<template>
  <div class="container">
    <div class="card card-primary">
      <div class="card-header">
        <h3 class="card-title">Добавити фільм</h3>
      </div>
      <form @submit.prevent="saveFilm">
        <div class="card-body">
          <div class="form-group">
            <label for="nameFilm">Назва фільма</label>
            <input
              type="text"
              class="form-control"
              id="nameFilm"
              placeholder="Введіть назву фільма"
              v-model="nameFilm"
              required
            />
          </div>
          <div class="form-group">
            <label for="description">Опис</label>
            <textarea
              type="text"
              class="form-control"
              id="description"
              placeholder="Текст"
              v-model="description"
              required
            ></textarea>
          </div>
          <div class="file-input">
              <label for="holovnaImg">Головна картинка</label>
              <input type="file" ref="fileInput" class="file-input__input" @change="handleFileChange" v-if="imageSrc === null" required>
              <button @click="triggerFileInput" class="btn btn-block btn-default"  v-if="imageSrc === null">Добавити</button>
              <div class="image-delete_btn">
                <div class="image-delete">
                  <img :src="imageSrc" class="imgInput" v-if="imageSrc">
                </div>
                <button @click="deleteImageSrc" class="btn btn-block btn-default"  v-if="imageSrc !== null">Видалити</button>
              </div>
          </div>
          <div class="file-input">
              <label for="holovnaImg">Галерея картинок</label>
              <input type="file" ref="fileInput2" class="file-input__input" @change="handleFileChange2" multiple>
              <button @click="triggerFileInput2" class="btn btn-block btn-default" v-if="galleryImgs == []">Добавити</button>
              <div class="image-delete_btn2">
                <div class="image-delete" v-for="(galleryImg, i) in galleryImgs" :key="i">
                  <img :src="galleryImg" class="imgInput" v-if="galleryImg">
                  <span class="deleteSpan" v-if="galleryImg" @click="deleteGalleryImg(i)">x</span>
                </div>
                <button @click="triggerFileInput2" class="btn btn-block btn-default"  v-if="galleryImgs != []">Добавити</button>
              </div>
          </div>
          <div class="form-group">
            <label>Посилання на трейлер</label>
            <input
              type="text"
              class="form-control"
              placeholder="Введіть посилання на трейлер"
              v-model="linkTrailer"
            />
          </div>
          <div class="form-group">
            <label>Тип фільма</label>
            <div class="form-main">
              <div class="form-check" v-for="(typeFilm, i) in typeFilms" :key="i" @click="pushTypeFilm(typeFilm, i)">
                <input class="form-check-input" v-model="typeFilm.checked" type="checkbox" :id="typeFilm.type">
                <label class="form-check-label" :for="typeFilm.type">{{ typeFilm.type }}</label>
              </div>
            </div>
          </div>
          <div class="form-group">
            <label>Фільм</label>
            <div class="form-main">
              <div class="form-check" v-for="(dataFilm, i) in dataFilms" :key="i">
                <input class="form-check-input" v-model="radioData" type="radio"
                 :id="dataFilm.name" name="name" :value="dataFilm.name">
                <label class="form-check-label" :for="dataFilm.name">{{ dataFilm.name }}</label>
              </div>
            </div>
          </div>
          <div class="form-group">
            <h5>SEO блок:</h5>
            <label>URL</label>
            <input
              type="text"
              class="form-control"
              placeholder="URL"
              v-model="url"
            />
          </div>
          <div class="form-group">
            <label>Title</label>
            <input
              type="text"
              class="form-control"
              placeholder="Title"
              v-model="title"
            />
          </div>
          <div class="form-group">
            <label>Keywords</label>
            <input
              type="text"
              class="form-control"
              placeholder="word"
              v-model="keywords"
            />
          </div>
          <div class="form-group">
            <label>Description</label>
            <textarea
              type="text"
              class="form-control"
              placeholder="Description"
              v-model="descriptionSeo"
            >
            </textarea>
          </div>
        </div>  
        <div class="card-footer">
          <button @click="saveFilm" class="btn btn-primary">Зберегти</button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>

//import { ref } from 'vue';
//import { storage } from '@/firebase/index.js'
import { getDatabase, ref as dbRef, set, push, update} from 'firebase/database'
import { getStorage, ref as storRef, uploadBytes, put, getDownloadURL } from 'firebase/storage'

export default {
  setup() {
   // const imageUrl = ref(null)

    // const uploudFile = async (event) => {
    //   const file = event.target.files[0]
    //   const storage = getStorage()
    //   const storageRef = ref(storage, 'images/' + file.name)
    //   //storageRef.put(file)
    //   await uploadBytes(storageRef, file);
    //   console.log(event.target.files[0])
    //   // const fileRef = storageRef.child(file.name)
    //   // await uploadBytes(fileRef, file)
    //   const url = getDownloadURL(storageRef)
    //   console.log(url)
    //   //imageUrl.value = url
    // }

    //return { imageUrl, uploudFile }
  },

  data() {
    return {
      nameFilm: '',
      description: '',
      imageSrc: null,
      imageSrc2: null,
      galleryImgs: [],
      linkTrailer: '',
      typeFilms: [
        {type: '2D', checked: false},
        {type: '3D', checked: false},
        {type: 'IMAX', checked: false},
      ],
      url: '',
      title: '',
      keywords: '',
      descriptionSeo: '',
      typeFilm: [],
      imageUrl: '',
      fileImgSrc: null,
      fileGalleryImg: [],
      dataFilms: [
        {name: 'Вже вийшов', checked: false},
        {name: 'Скоро вийде', checked: false}
      ],
      radioData: '',
      dataFilm: [],
    }
  },
  methods: {
    handleFileChange(event) {
      this.fileImgSrc = event.target.files[0];
      this.imageSrc = URL.createObjectURL(this.fileImgSrc);
    },
    handleFileChange2(event) {
      this.fileGalleryImg = [...event.target.files]
      for(let image of this.fileGalleryImg){
        this.imageSrc2 = URL.createObjectURL(image)
        this.galleryImgs.push(this.imageSrc2)
      }
    },
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    triggerFileInput2(){
      this.$refs.fileInput2.click();
    },
    deleteImageSrc(){
      this.imageSrc = null
    },
    deleteGalleryImg(i){
      this.galleryImgs.splice(i, 1)
    },
    pushTypeFilm(typeFilm, i){
      if(!this.typeFilm.includes(typeFilm.type) && typeFilm.checked === false){
        this.typeFilm.push(typeFilm.type)
        console.log(this.typeFilm)
      } else if(this.typeFilm.includes(typeFilm.type) && typeFilm.checked === true){
        this.typeFilm.forEach(function (item, index, typeFilms) {
          if (typeFilm.type === item) {
            i = index;
          }
        });
        this.typeFilm.splice(i, 1)
      }
    },
    async saveFilm(){
      const db = getDatabase()
      const storage = getStorage()
      // Додайте фільм до бази даних Firebase
      const path = `films/${this.nameFilm}`
      const moviesRef = dbRef(db, path);
      //const newMovieRef = push(moviesRef);
      set(moviesRef, {
        id: moviesRef.key,
        nameFilm: this.nameFilm,
        description: this.description,
        imageFirst: this.fileImgSrc,
        galleryImgs: this.fileGalleryImg,
        //galleryImgs: this.galleryImgs,
        linkTrailer: this.linkTrailer, 
        typeFilm: this.typeFilm,
        radioData: this.radioData,
        url: this.url,
        title: this.title, 
        keywords: this.keywords,
        descriptionSeo: this.descriptionSeo
      });

        // Завантажити головну картинку до Firebase Storage та зберегти посилання на неї до фільму в Firebase Realtime Database
      if (this.fileImgSrc) {
        const storageRef = storRef(storage, `films/${moviesRef.key}/imageFirst`);
        uploadBytes(storageRef, this.fileImgSrc).then((snapshot) => {
          getDownloadURL(snapshot.ref).then((url) => {
            update(moviesRef, {
              imageFirst: url
            });
          });
        });
      }

        // Завантажити картинки галереї до Firebase Storage та зберегти посилання на них до фільму в Firebase Realtime Database
      const galleryImages = [];

      this.fileGalleryImg.forEach((galleryImg, index) => {
        const storageRef = storRef(storage, `films/${moviesRef.key}/galleryImgs/${index}`);
        uploadBytes(storageRef, galleryImg).then((snapshot) => {
          getDownloadURL(snapshot.ref).then((url) => {
            galleryImages[index] = url;
            if (galleryImages.length === this.fileGalleryImg.length) {
              update(moviesRef, {
                galleryImages
              });
            }
          });
        });
      });

      this.$router.push('films')

        // Очистіть поля форми
      this.nameFilm = ''
      this.description = ''
      this.imageSrc = null
      // this.imageSrc2 = null
      this.galleryImgs = []
      this.linkTrailer = ''
      this.typeFilms = [
        {type: '2D', checked: false},
        {type: '3D', checked: false},
        {type: 'IMAX', checked: false}
      ],
      this.url = ''
      this.title = ''
      this.keywords = ''
      this.descriptionSeo = ''
    }
  },

}
</script>
<style lang="scss">

.container{
    width: 70%;
}
// input[type="file"] {
//   display: block;
//   opacity: 1;  
// }

.upload-button {
  display: inline-block;
  border: 1px solid #ccc;
  padding: 6px 12px;
  cursor: pointer;
}

.upload-preview {
  display: inline-block;
  width: 100px;
  height: 100px;
  border: 1px solid #ccc;
  margin-left: 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
label{
  display: block;
  text-align: left;
}
.file-input {
  position: relative;
}
.file-input__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 30%;
  height: 70px;
  cursor: pointer;
}
.btn-block {
    display: block;
    width: 30%;
}
.imgInput{
  width: 250px;
  height: 250px;
  display: block;
}
.image-delete_btn{
  display: flex;
  gap: 40px;
  align-items: end;
  margin-bottom: 0.5rem;
}
.image-delete_btn2{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: end;
  margin-bottom: 0.5rem;
}
.deleteSpan{
  position: absolute;
  top: -8px;
  right: -8px;
  border: 1px solid black;
  background: white;
  border-radius: 50%;
  color: black;
  width: 26px;
  cursor: pointer;
}
.image-delete{
  position: relative;
}
.form-main{
  display: flex;
  gap: 20px;
}
.form-check-input {
  display: block;
}
.form-group{

  h5{
    text-align: start;
  }
}
.card-footer{
  display: flex;
  gap: 30px;
  justify-content: center;
}
</style>
